.about-us {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 100px;
    padding-left: 240px;
    padding-right: 150px;
    background-color: #f9f9f9;
  }
  
  .about-us-text {
    flex: 1;
    padding: 20px;
    text-align: left;
  }
  
  .about-us-text h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #333;
  }
  
  .about-us-text p {
    font-size: 1.1em;
    color: #555;
    line-height: 1.6;
  }
  
  .about-us-image {
    flex: 1;
    padding: 20px;
    text-align: center;
  }
  
  .about-us-image img {
    max-width: 100%;
    height: 430px;
    border-radius: 10px;
  }
  
  @media (max-width: 768px) {
    .about-us {
      flex-direction: column;
    }
  
    .about-us-image {
      margin-top: 20px;
    }
  }
  